import * as turf from '@turf/turf'
import Map from '../../components/Map'
import graphs from './graphs'

const state = {
  shpUploading: false,
  drawing: false,
  polyArea: null,
  cellNames: null,
  userPolygon: null,
  ecoregions:null,
  polygonSW: null,
  polygonNE: null,
  draw: null,
  map: null,
  payloadForIntersection: {},
  metricsToAssess : ["GRID_ID", "ECOREG"],
  numCells: 0,
  allMetrics: { //this object is used to populate the template
    "Cell ID" : "GRID_ID",
    "Ecoregion" : "ECOREG",
    "One Fish" : "1Fish",
    "Two Fish" : "2Fish",
    "Red Fish" : "RedFish",
    "Blue Fish" : "BlueFish"
  }
};

const getters = {
  isDrawing: state => state.drawing, //this is the drawing Boolean
  shpUploading: state =>state.shpUploading,
  draw: state =>state.draw, //this is the mapbox Draw object
  polygonArea: state => state.polyArea,
  cellsInBox: state => state.cellNames,
  ecoregionsInBox: state => state.ecoregions,
  metricsToAssess: state => state.metricsToAssess,
  allMetrics: state => state.allMetrics,
  userPolygon: state => state.userPolygon,
  numCells: state => state.numCells,
  map: state => state.map
};

const actions = {
  toggleDrawing: ({ commit }) => { //commit is a given function that's used to call mutations
    commit('setDrawing');  //toggle the Boolean
  },

  setMapInState: ({ commit }, payload) => {
    commit('setMap', payload.map);
  },

  updatePolyArea: ({ commit }, payload) => {
    commit('updateArea', payload.polygon);
    commit('getDrawnPolyBbox', payload);
    commit('getCells', payload);
    commit('intersectAndParse', payload);
    setTimeout(function() {
      payload["numCells"] = state.numCells;
      commit('updateBarChart', payload);
    }, 200);
  },

  deleteSketchedPolygon: ( { commit }, payload) => {
    commit('clearDrawnPoly', payload);
  },

  deleteUploadedPoly: ({ commit }, payload) =>{
    commit('removeUploadedPoly', payload);
  },

  updateMetricsToAssess: ({ commit }, payload) => {
    commit('updateMetricsArray', payload);
  },

  setDrawObject: ({ commit }, payload) =>{
    commit('setDrawObj', payload) //this is the mapbox Draw object
  },

  storeUserPolygon: ({ commit }, payload) =>{
    commit('setUserPolygon', payload);
  },

  setUploadingStatus: ({ commit }, payload)=>{
    commit('setUploading', payload)
  }
};

const mutations = {

  setUploading: (state, payload) =>{
    state.shpUploading = payload;
  },

  setUserPolygon: (state, payload) =>{
    state.userPolygon = payload.polygon;
  },

  removeUploadedPoly: (state, payload) =>{
    console.log("removing")
    var map = payload.map;
    var mapLayer = map.getLayer('userPoly');
    if(typeof mapLayer !== 'undefined') {
      // Remove map layer & source.
      map.removeLayer('userPoly').removeSource('userPoly');
    }
    state.userPolygon = null;
    state.polyArea = null;
    state.ecoregions = null;
    state.cellNames = null;
  },

  setDrawObj:(state, payload) =>{
    state.draw = payload.draw
  },


  updateMetricsArray: (state, payload) => {
    state.metricsToAssess = payload;
  },

  setDrawing: (state, drawing) =>{
    state.drawing = !state.drawing;
  },

  setMap: (state, payload) =>{
    state.map = payload;
  },

  clearDrawnPoly: (state, payload) =>{
    payload.draw.deleteAll();
    state.userPolygon = null;
    state.polyArea = null;
    state.ecoregions = null;
    state.cellNames = null;
  },

  updateArea: (state, poly) =>{
    // state.userPolygon = poly; //I thought I needed this, but it was causing the map background focus error, and everything works without it.
    var ref = this
    var drawnPolygon = poly.features[0];
    var polygonId = drawnPolygon.id.toString();
    var area = turf.area(drawnPolygon);
    var km2 = Math.round((area/1000000)*100)/100
    km2 = km2.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    state.polyArea  = km2;
  },

  getDrawnPolyBbox: (state, payload) => {
    var poly = payload.polygon;
    var map = payload.map;
    var ref = this
    var drawnPoly = poly.features[0];
    var polygonCenter = turf.center(drawnPoly).geometry.coordinates;
    var polygonBbox = turf.bbox(drawnPoly);
    var polygonSW = map.project([polygonBbox[0], polygonBbox[1]]);
    var polygonNE = map.project([polygonBbox[2], polygonBbox[3]]);
    state.polygonNE = polygonNE;
    state.polygonSW = polygonSW;

  },

  getCells: (state, payload) => {
    var drawnPoly = payload.polygon.features[0];
    state.payloadForIntersection.id = drawnPoly.id;
    state.payloadForIntersection.drawnPolygon = drawnPoly;
    state.payloadForIntersection.layerName = "cells"
    state.payloadForIntersection.cells = JSON.parse(JSON.stringify(payload.map.queryRenderedFeatures([[state.polygonSW.x, state.polygonSW.y], [state.polygonNE.x, state.polygonNE.y]], {layers: ['squares-webmerc']})))
  },

  intersectAndParse: (state, payload) =>{
    var drawnPoly = payload.polygon.features[0];
    var cells = state.payloadForIntersection.cells;

    var cellData = [];
    for (var i=0; i < cells.length; i++){
        if (turf.intersect(cells[i], drawnPoly)){
          var thisHexData = {}
          for (var j=0; j < state.metricsToAssess.length; j++){
            thisHexData[state.metricsToAssess[j]] = cells[i].properties[state.metricsToAssess[j]];
          }
          cellData.push(thisHexData)
        }
    }

    //get unique cell data... sometimes returning duplicates.  See:
    //https://dev.to/marinamosti/removing-duplicates-in-an-array-of-objects-in-js-with-sets-3fep
    cellData =Array.from(new Set(cellData.map(a =>a.GRID_ID)))
      .map(GRID_ID => {
        return cellData.find(a => a.GRID_ID === GRID_ID)
      })

    //get list of cell IDs & ecoregions
    var cellIDs =  []
    var ecoregions = []
    for (i=0; i<cellData.length; i++){
      cellIDs.push(cellData[i].GRID_ID)
      ecoregions.push(cellData[i].ECOREG)
    }
    ecoregions = [...new Set(ecoregions)]; //there can be multiple cells in 1 ecoregion, so make another Set
    var prettyNames = cellIDs.join(', ');
    var prettyEcoregions = ecoregions.join(', ');
    state.ecoregions = prettyEcoregions
    state.cellNames = prettyNames;
    state.numCells = cellIDs.length;
  }


};

export default {
  state,
  getters,
  actions,
  mutations
};
