<template>
  <div>

  <v-btn-toggle>
    <v-btn color="accent" @click="shpUpBtn" class="ma-4 white--text" ><v-icon color="white">mdi-upload</v-icon>
      <span>Upload SHP</span>
    </v-btn>
  </v-btn-toggle>
  <v-spacer></v-spacer>
  <p class="ma-4" v-if="shpUploading">Uploading &amp; Converting SHP
    <v-progress-circular indeterminate color="error"></v-progress-circular>
  </p>
  <form enctype="multipart/form-data" method="post" id="uploadForm" ref="uploadForm">
    <div class="field" style="display: none;"> <!-- Hide the input and have the button trigger it -->
      <label class="file-upload">
         <input type="file" name="file" id="inFile" ref="upFile" @change="shpUpload"/>
      </label>
    </div>
  </form>
  </div>
</template>


<script>


  import { loadModules }  from 'esri-loader';
  import { mapActions, mapGetters } from 'vuex';

  export default {
    name: 'UploadSHP',

    props: {

    },
    computed: {
      ...mapGetters(['map',  'metricsToAssess', 'userPolygon', 'draw', 'shpUploading']),
    },
    methods:{
      ...mapActions(['updatePolyArea', 'updateMetricsToAssess', 'deleteSketchedPolygon', 'storeUserPolygon', 'deleteUploadedPoly',  'setUploadingStatus']),

      shpUpBtn: function(){
        this.$refs.upFile.click()
      },
      shpUpload: function(e){
        var ref = this;
        ref.setUploadingStatus(true);
        console.log("Starting upload!")
        console.log(e)
        this.removeLayers(ref.map)
        this.updateMetricsToAssess(this.metricsToAssess);

        loadModules(['esri/tasks/Geoprocessor', 'esri/request'], { css: false }).then(([Geoprocessor, esriRequest])=>{
          const gp = new Geoprocessor("https://cumulus.tnc.org/arcgis/rest/services/easterndiv/featureToGeoJSON/GPServer/featureToGeoJSON")
          const gpUploadURL = "https://cumulus.tnc.org/arcgis/rest/services/easterndiv/featureToGeoJSON/GPServer/uploads/upload"
          var fileName = e.target.value.toLowerCase();
          uploadFile(fileName);

          function uploadFile(fileName) {
            console.log("uploading file!");
            var name = fileName.split(".");
              name = name[0].replace("c:\\fakepath\\", "");
              var upload = esriRequest(
                 gpUploadURL,
                 {
                    body: ref.$refs.uploadForm,
                    method: "post",
                    responseType: "text"
                  }
                  ).then(uploadSucceeded, errBack);
              }

          function uploadSucceeded(response){
            //example from https://community.esri.com/groups/arcgis-server-with-javascript-api/blog/2019/10/22/js-api-v4-uploading-a-file-to-a-gp-service
            var element = document.createElement('html');
            var itemID = "";
            var itemURL = "";
            element.innerHTML = response.data;
            var tdElements = element.getElementsByTagName('td');
            for (let tdElement of tdElements){
              if (tdElement.innerHTML == "Item ID:"){
                 itemID = tdElement.nextElementSibling.firstChild.innerHTML;
              }
            }
            if (itemID == ""){
              console.log("Could not get file item id");
              return
            }

              //submit the GP Task by passing the itemID info the input parameter
              var params = {
                 zippedShapefile: "{'itemID':'" + itemID + "'}"
              };

            gp.submitJob(params).then(function(jobInfo) {
              var jobid = jobInfo.jobId;
              var options = {
                interval: 500,
                statusCallback: function(j) {
                  var message = j.messages[j.messages.length -1].description;
                  console.log(message);
                }
              };

              gp.waitForJobCompletion(jobid, options).then(function() {
                console.log(jobid)
                gp.getResultData(jobid, "outputJSON").then(function(result){
                  var resURL =result.value.url
                  console.log(resURL)
                  ref.drawUploaded(ref.map, resURL)
                }, function(error){
                  console.error(error);
                });
              });
            });
            function displayResult(result){
              console.log(result)
              ref.drawUploaded(ref.map, result)
            }
          }
          function errBack(error){
            console.log("gp error: ", error);
          }
        });
      },

      removeLayers: function(map){
        var ref =this;
        try{
          var drawing = {
            "draw" : ref.draw
          }
          if (drawing != undefined){
            ref.deleteSketchedPolygon(drawing)
          }
        }
        catch{console.log("No sketched poly")}
        var payload = {"map": map}
        ref.deleteUploadedPoly(payload);
      },

      drawUploaded: function(map, resURL){
        var ref = this;
        console.log("drawing uploaded");
        var request = new XMLHttpRequest();
        request.open('GET', resURL, true);
        request.responseType = 'blob';
        request.onload = function() {
            var reader = new FileReader();
            reader.readAsText(request.response);
            reader.onload =  function(e){
              var uploadedPoly = e.target.result;
              uploadedPoly = JSON.parse(uploadedPoly)
              map.addSource('userPoly', {
                'type' : 'geojson',
                'data' : uploadedPoly
              });
              map.addLayer({
                'id': 'userPoly',
                'source': 'userPoly',
                "type": "fill",
                "paint": {
                  "fill-color": "#00ffff"
                }
              })
              // ref.updateMetricsToAssess(ref.metricsToAssess);
              var payload = {
                "polygon" : uploadedPoly,
                "map" : map
              }
              ref.storeUserPolygon(payload);
              ref.updatePolyArea(payload);
            };
        };
        request.send();
        ref.setUploadingStatus(false);
      }
    },
    data: () => ({
      showUploadForm: false
    })
  }
</script>
